:host {
  display: block;
}

.primary-button {
  cursor: pointer;
  margin-left: 10vh;
  margin-top: 2vh;
  display: flex;
  width: 50%;
  height: 30px;
  padding: 7px 11px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 2px;
  border: 1px solid #000;
  background: #c4ebda;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.05);
  color: #000;
  font-family: 'IBM Plex Mono', monospace;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px; /* 66.667% */
}

.secondary-button {
  cursor: pointer;
  background: #eee;
  border: 1px solid gray;
  color: gray;
  font-family: 'IBM Plex Mono', monospace;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px; /* 66.667% */
  width: 300px;
  padding: 10px;
}

.secondary-button:disabled .primary-button:disabled {
  background-color: #ccc; /* or any other color to indicate it's disabled */
  cursor: not-allowed;
}