import { Component, Host, Prop, h } from '@stencil/core';

@Component({
  tag: 'app-button',
  styleUrl: 'app-button.css',
  scoped: true,
})
export class AppButton {
  @Prop() text: string;
  @Prop() clickAction: (arg1?: any) => Promise<void> | void;
  @Prop() classTier: 'primary' | 'secondary' = 'primary';
  @Prop() active: boolean = true;
  @Prop() loading: boolean = false;

  render() {
    console.log('Button type: ', this.classTier);
    return (
      <button class={`${this.classTier}-button`} disabled={!this.active} onClick={() => this.clickAction()}>
        {this.loading ? <ion-spinner name="dots"></ion-spinner> : this.text}
      </button>
    );
  }
}
